.ResumeSorceCustomFilterModal {
  z-index: 0 !important;
  .filterMainDiv {
    background-color: #fff;
    width: 60%;
    height: 40%;
    overflow: auto;
    border-radius: 4px;
    position: absolute;
    top: 30%;
    left: 25%;
    .HeaderFilter {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      font-weight: 700;
      color: #000000;
      padding: 10px 20px;
      border-bottom: 1px solid #36363659;
      img {
        cursor: pointer;
      }
    }
    .AddMoreBtn {
      margin: 20px;
      display: flex;
    }
    .InputDiv {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 20px 20px 0;
      .CustomResumeSourceFilterDropDown {
        width: 30%;
      }
      .CustomResumeSourceFilterInput {
        width: 30%;
        .inputArea {
          .MuiInputBase-root {
            background-color: #fff;
            border: 1px solid #cccccc;
            border-radius: 4px;
            height: 34px;
            font-size: 12px;
          }
        }
      }
    }
  }
}
